import React, { useContext, useState } from 'react';
import { useEffect } from 'react';
import Header from './Components/Header';
import MenuInferior from '../Components/MenuInferior';
import MVCard from '../MarketValue/MVCard';
import { withRouter } from 'react-router-dom';
import Bitcoin from '../imagesIcons/Bitcoin.svg';
import Ethereum from '../imagesIcons/Ethereum.svg'
import Sapiens from '../imagesIcons/Sapiens.svg';
import '../Layout/Style.css';
import WalletMenu from './Components/WalletMenu';
import { fetchPriceInfo } from '../services/fetch';
import { getValueByKey, showMessage } from '../services/utils';
import graphIcon from '../imagesIcons/graphIcon.svg';
import 'materialize-css';
import { Carousel } from 'react-materialize';

const SubHeader = ({ prices }) => (
  <div style={{
    background: 'var(--grey)',
    width: '100vw',
    padding: '16px 16px 8px 16px',
    marginTop: '-12px',
    borderBottomLeftRadius: '16px',
    borderBottomRightRadius: '16px',
  }}>
    <div style={{display: 'flex', justifyContent: 'center'}} >
      {
        prices.map((item, i) => (
          <div key={i} style={{ color: 'var(--light-grey)', fontSize: '12px' }}>
            <span>{ item.symbol } { item.variation }%</span>
            <span style={{ margin: '0 5px' }}>
              { i >= 0 && i < prices.length - 1 ? '•' : '' }
            </span>
          </div>
        ))
      }
    </div>
  </div>
);

const MarketValue = ({ history }) => {

  const [prices, setPrices] = useState([])
  const [counter, setCounter] = useState(0)
  const loginData  = getValueByKey('loginData')
  const { username } = loginData

  useEffect(() => {
    if (loginData === 'emptyData') {
      history.push("/")
    }
  }, [])

  useEffect(() => {
    setTimeout(() => setCounter(counter + 1), 180000)
    console.log(counter)
  }, [counter])

  useEffect(() => {
    if (prices.length === 0 || counter >= 0) {
     fetchPriceInfo()
      .then((response) => {
        const {  BTC, ETH, SPN, USD } = response.data
        console.log(response.data)
        setPrices([
          { name: "Sapiens",
            symbol: "SPN",
            priceD: parseFloat(SPN[0] / USD[0]).toFixed(2),
            priceR: parseFloat(SPN[0]).toFixed(2),
            variation: parseFloat(((SPN[0]/SPN[1]) - 1) * 100).toFixed(2),
            icon: Sapiens,
          },
          { name: "Bitcoin",
            symbol: "BTC",
            priceD: parseFloat(BTC[0] / USD[0]).toFixed(2),
            priceR: parseFloat(BTC[0]).toFixed(2),
            variation: parseFloat(((BTC[0]/BTC[1]) - 1) * 100).toFixed(2),
            icon: Bitcoin, 
          },
          { name: "Ethereum",
            symbol: "ETH",
            priceD: parseFloat(ETH[0] / USD[0]).toFixed(2),
            priceR: parseFloat(ETH[0]).toFixed(2),
            variation: parseFloat(((ETH[0]/ETH[1]) - 1) * 100).toFixed(2),
            icon: Ethereum,
          },
        ])})
    }
    console.log(prices)
  }, [counter])

  if (prices.length === 0) {
    return (
    <div>
      <p> Loading... </p>
    </div>
    )
  }
  return (
    <div className="MV-content-container">
      <Header username={ username } to="/dashboard" >
        <div className="MV-header-box-2">
          <img className="icon-size-2 rounder-icon white1-background" src={graphIcon} />
          <p className="text-size-1">{prices[2].variation >= 0 ? `+ ${prices[2].variation}` : `- ${prices[2].variation}`}%</p>
          <i
            className="material-icons"
            style={{ color: "var(--light-grey)" }}
          >arrow_drop_up</i>
        </div>   
      </Header>

      <SubHeader prices={prices} />

      <Carousel
        carouselId="Carousel-cards"
        options={{
          dist: 0,
          padding: 0,
          numVisible: 3,
          fullWidth: false,
          indicators: false,
        }}
      >
        { prices.map((item, i) => (
          <div key={i}>
            <MVCard coin={item} />
          </div>
        ) ) }
      </Carousel>
      <WalletMenu />
      <MenuInferior />
    </div>
  )
}

export default withRouter(MarketValue);