import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom';
import MenuInferior from '../Components/MenuInferior';
import WalletMenu from './Components/WalletMenu';
import Header from './Components/Header';
import { getValueByKey, sum } from '../services/utils';

const NEUTRALIZACAO = 0;
const COMPENSACAO = 1;
const COMPRA = 2;
const DISPONIVEL = 3;


const CreditCell = ({ children, balance, color, percentage }) => (
  <div style={{
    display: 'flex',
    justifyContent: 'space-between',
    width: '100%',
    marginBottom: '12px', 
  }}>
    <div style={{
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'flex-start',
    }}>
      <li style={{ fontSize: '16px' }}>{ children }</li>
      <span style={{
        color: (color || 'red'),
        fontSize: '24px',
      }}>SPN { balance }</span>
    </div>
    <span style={{
      borderRadius: '4px',
      backgroundColor: (color || 'red'),
      width: '22px',
      height: `${60 * (percentage || 0.01)}px`,
      margin: '2px 0',
    }}></span>
  </div>
);

const SapiensBalance = ({ history }) => {
  
  const loginData = getValueByKey('loginData');
  /* const { username, wallet: { balance } } = loginData */
  const [soma, setSum ] = useState('emptySum')
  const [username1, setUsername] = useState('name')
  const [balance1, setBalance] = useState('balance')
  
  useEffect(() => {
    if (loginData === 'emptyData') {
      history.push("/")
    }
  },[loginData])

  useEffect(() => {
    if (loginData !== 'emptyData') {
      const { username, wallet: { balance } } = loginData
      setUsername(username)
      setBalance(balance)
      setSum(sum(loginData));
    }
  },[])

  return (
    <div className="main-container">

      <Header username={ username1 } to="/dashboard">
        <div style={{ textAlign: 'center', fontSize:'24px', color: 'var(--light-grey)' }}>
          SPN {parseFloat(soma).toFixed(2)}
        </div>
      </Header>

      <div className="screen" style={{ alignItems: 'flex-start' }}>
        <div className="balance-title">
          <p className="black-text"><strong>Resumo da conta</strong></p>
        </div>

        <CreditCell
          balance={ balance1[NEUTRALIZACAO] }
          color="#4ab8df"
        >
          Por plano de <strong>neutralização</strong>
        </CreditCell>
        <CreditCell
          balance={ balance1[COMPENSACAO] }
          color="#16d28e"
        >
          Por neutralização <strong>ecológica</strong>
        </CreditCell>
        <CreditCell
          balance={ parseFloat(balance1[DISPONIVEL]).toFixed(2) }
          color="#ff914d"
          percentage={1}
        >
          Sapiens-coins <strong>disponíveis</strong>
        </CreditCell>

        <div className="balance-info">
          <p className="darkGrey-text"><strong>Transações</strong></p>
          <p className="darkGrey-text">Adquiridas</p>
          <p>SPN {/* {balance[COMPRA]} */}</p>
          <p className="darkGrey-text">Enviadas</p>
          <p>SPN {/* {balance[COMPRA]} */}</p>
        </div>
      </div>
      <WalletMenu />
      <MenuInferior />
    </div>
  )
}

export default withRouter(SapiensBalance);